import Skeleton from '@mui/material/Skeleton';
import PropTypes from 'prop-types';
import { useEffect, useState } from 'react';

import styles from './GameCard.module.css';

export const GameCardContainer = ({ children, clickHandler, index }) => {
  console.log(clickHandler);
  return (
    <div className={styles.cardContainer}
onClick={() => clickHandler(index)}>
      {children}
    </div>
  );
};

GameCardContainer.propTypes = {
  children: PropTypes.element,
  clickHandler: PropTypes.func,
  index: PropTypes.number,
};

export const GameCard = ({ cardData }) => {
  const { isClosed, url, name } = cardData;
  const [imageAddress, setImageAddress] = useState();

  useEffect(() => {
    import(`../../../public/decks/default/${name}.jpg`).then((image) => {
      setImageAddress(image.default);
    });
  });

  return imageAddress ? (
    <>
      <img alt={name}
className={styles.cardImage}
src={imageAddress} />
      {isClosed ? <div className={styles.closedImage}>X</div> : null}
    </>
  ) : (
    <Skeleton variant='rectangle'
className={styles.skeleton} />
  );
};

GameCard.propTypes = {
  cardData: PropTypes.object,
  index: PropTypes.number,
  clickHandler: PropTypes.func,
};
