import { useContext } from 'react';
import { Container } from '@mui/material';

import { GameBoardContext } from '@contexts/boardContext';

import CreateGameButton from './CreateGameButton';
import JoinGameCard from './JoinGameCard';
import LeaveGameButton from './LeaveGameButton';
import styles from './GameAccess.module.css';

const GameAccess = () => {
  const { gameData } = useContext(GameBoardContext);

  return (
    <Container className={styles.wrapper}>
      {gameData?.gameId ? (
        <>
          <LeaveGameButton />
          <div>{`Playing As: ${gameData?.player?.guessingTarget}`}</div>
        </>
      ) : (
        <>
          <CreateGameButton />
          <JoinGameCard />
        </>
      )}
    </Container>
  );
};

export default GameAccess;
