import { useAuth0 } from '@auth0/auth0-react';
import PropTypes from 'prop-types';
import { useContext, useState } from 'react';
import { Button, Container, TextField } from '@mui/material';

import { AuthContext } from '@contexts/authContext';
import { GameBoardContext } from '@contexts/boardContext';
import { joinGame } from '@services/api';

const JoinGameCard = ({ setGameId, setPlayer }) => {
  const [gameCode, setGameCode] = useState();
  const { user } = useAuth0();
  const { setGameData } = useContext(GameBoardContext);
  const { token } = useContext(AuthContext);
  const onClick = async (e) => {
    e.preventDefault();
    const body = JSON.stringify({
      user: user.email,
    });

    const data = await joinGame({ body, token, gameId: gameCode });
    setGameData(data);
  };

  return (
    <Container style={{ display: 'inline' }}>
      <TextField
        variant='outlined'
        label='Game Code'
        size='small'
        style={{ marginRight: '5px' }}
        onChange={(e) => {
          setGameCode(e.target.value);
        }}
      />
      <Button onClick={onClick} variant='contained'>
        Join
      </Button>
    </Container>
  );
};

JoinGameCard.propTypes = {
  setGameId: PropTypes.func,
  setPlayer: PropTypes.func,
};
export default JoinGameCard;
