const apiURL = process.env.REACT_APP_API_URL;

const getGameCards = async ({ token }) => {
  if (token) {
    const res = await fetch(`${apiURL}/cards`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
    const data = await res.json();
    return data;
  }
};

const createNewGame = async ({ body, token }) => {
  if (token) {
    const res = await fetch(`${apiURL}/game`, {
      body,
      headers: {
        'Content-Type': 'application/json',
        authorization: `Bearer ${token}`,
      },
      method: 'POST',
    });
    const data = await res.json();
    return data;
  }
};

const joinGame = async ({ body, token, gameId }) => {
  if (token) {
    const res = await fetch(`${apiURL}/game/${gameId}`, {
      body,
      headers: {
        'Content-Type': 'application/json',
        authorization: `Bearer ${token}`,
      },
      method: 'PUT',
    });
    const data = await res.json();
    return data;
  }
};

const leaveGame = async ({ token, gameId }) => {
  if (token) {
    const res = await fetch(`${apiURL}/game/${gameId}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
      method: 'DELETE',
    });
  }
};

export { createNewGame, getGameCards, joinGame, leaveGame };
