import { useAuth0 } from '@auth0/auth0-react';
import { useContext } from 'react';
import Button from '@mui/material/Button';

import { AuthContext } from '@contexts/authContext';
import { GameBoardContext } from '@contexts/boardContext';
import { createNewGame } from '@services/api';

const CreateGameButton = () => {
  const { user } = useAuth0();
  const { token } = useContext(AuthContext);
  const { setGameData } = useContext(GameBoardContext);
  const onClick = async (e) => {
    e.preventDefault();
    try {
      const body = JSON.stringify({
        user: user.email,
      });
      const data = await createNewGame({ token, body });
      setGameData(data);
    } catch (error) {
      console.log(error);
    }
  };
  return <Button onClick={onClick}>Create Game</Button>;
};

export default CreateGameButton;
