import { useAuth0 } from '@auth0/auth0-react';
import { useContext } from 'react';
import Button from '@mui/material/Button';

import { STATE_LOCAL_STORAGE } from '@constants';
import { GameBoardContext } from '@contexts/boardContext';
import { clearBoard } from '@services/game';

const ResetButton = () => {
  const { gameCards, setGameCards } = useContext(GameBoardContext);
  const { isLoading, isAuthenticated } = useAuth0();
  const onClick = () => {
    const cleanBoard = clearBoard({ gameBoard: gameCards });
    localStorage.removeItem(STATE_LOCAL_STORAGE);
    setGameCards(cleanBoard);
  };

  return (
    !isLoading && isAuthenticated && <Button onClick={onClick}>Reset</Button>
  );
};

export default ResetButton;
