import { useContext } from 'react';
import { Button } from '@mui/material';

import { AuthContext } from '@contexts/authContext';
import { DEFAULT_GAME_DATA, GameBoardContext } from '@contexts/boardContext';
import { leaveGame } from '@services/api';

const LeaveGameButton = () => {
  const { setGameData, gameData } = useContext(GameBoardContext);
  const { token } = useContext(AuthContext);

  const onClick = async () => {
    await leaveGame({ token, gameId: gameData.gameId });
    setGameData(DEFAULT_GAME_DATA);
  };
  return (
    <div>
      {`Game Id: ${gameData.gameId}`}
      <Button onClick={onClick} variant='contained'>
        Leave Game
      </Button>
    </div>
  );
};

export default LeaveGameButton;
