import { useContext, useEffect } from 'react';

import { STATE_LOCAL_STORAGE } from '@constants';
import { AuthContext } from '@contexts/authContext';
import { GameBoardContext } from '@contexts/boardContext';
import { GameCard, GameCardContainer } from '@components/GameCard';
import { getGameCards } from '@services/api';

import styles from './GameBoard.module.css';

const GameBoard = () => {
  const { token } = useContext(AuthContext);
  const { setGameCards, gameCards } = useContext(GameBoardContext);
  useEffect(() => {
    async function loadCards() {
      let data = await getGameCards({ token });
      const boardState = JSON.parse(localStorage.getItem(STATE_LOCAL_STORAGE));
      if (boardState) {
        data = data?.map((card, i) => ({
          ...card,
          isClosed: Boolean(boardState[i]),
        }));
      }
      setGameCards(data);
    }
    loadCards();
  }, [token]);

  const clickPicture = (index) => {
    const newCards = [...gameCards];
    newCards[index].isClosed = !newCards[index].isClosed;
    setGameCards(newCards);
    localStorage.setItem(
      STATE_LOCAL_STORAGE,
      JSON.stringify(newCards.map((card) => Number(!!card.isClosed)))
    );
  };
  const board = gameCards?.map((picture, i) => {
    return (
      <GameCardContainer key={i} clickHandler={clickPicture} index={i}>
        <GameCard index={i} cardData={picture} clickHandler={clickPicture} />
      </GameCardContainer>
    );
  });
  return <div className={styles.wrapper}>{board}</div>;
};

export default GameBoard;
