import PropTypes from 'prop-types';
import React, { useState } from 'react';

const GameBoardContext = React.createContext();

const DEFAULT_GAME_DATA = {
  gameId: '',
  player: {},
};

const GameBoardContextProvider = ({ children }) => {
  const [gameCards, setGameCards] = useState([]);
  const [gameData, setGameData] = useState(DEFAULT_GAME_DATA);
  return (
    <GameBoardContext.Provider
      value={{
        gameCards,
        gameData,
        setGameCards,
        setGameData,
      }}
    >
      {children}
    </GameBoardContext.Provider>
  );
};

GameBoardContextProvider.propTypes = {
  children: PropTypes.element,
};

export { DEFAULT_GAME_DATA, GameBoardContext, GameBoardContextProvider };
